  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (max-width: 767.98px) {
  main {
    min-height: calc(100vh - 612px);
  }
}

